/** @format */

import React from "react";
import firebase from "./firebase";
function getDaysOfMonth(year, month) {
  return new Date(year, month + 1, 0).getDate();
}
const removeArray = (array, arrayRemove) => {
  var filtered = array.filter(function (value, index, arr) {
    return value > 0;
  });
  return filtered;
};
function Main() {
  const [Nam, SetNam] = React.useState(window.localStorage.getItem("Nam"));
  const [DL, SetDL] = React.useState([]);
  const [dltong, Setdltong] = React.useState(
    JSON.parse(window.localStorage.getItem("tong"))
  );
  const [MangTongLo, SetMangTongLo] = React.useState([
    "Tổng 0",
    "Tổng 1",
    "Tổng 2",
    "Tổng 3",
    "Tổng 4",
    "Tổng 5",
    "Tổng 6",
    "Tổng 7",
    "Tổng 8",
    "Tổng 9",
    "Tổng +0",
    "Tổng +1",
    "Tổng +2",
    "Tổng +3",
    "Tổng +4",
    "Tổng +5",
    "Tổng +6",
    "Tổng +7",
    "Tổng +8",
    "Tổng +9",
  ]);

  const [MangSo, SetMangSo] = React.useState([
    "00 19 91 28 82 37 73 46 64 55",
    "01 10 29 92 38 83 47 74 56 65",
    "02 20 11 39 93 48 84 57 75 66",
    "03 30 12 21 49 94 58 85 67 76",
    "04 40 13 31 22 59 95 68 86 77",
    "05 50 14 41 23 32 69 96 78 87",
    "06 60 15 51 24 42 33 79 97 88",
    "07 70 16 61 25 52 34 43 98 89",
    "08 80 17 71 26 62 35 53 44 99",
    "09 90 18 81 27 72 36 63 45 54",
    "00 11 22 33 44 55 66 77 88 99",
    "01 12 23 34 45 56 67 78 89 90",
    "02 13 24 35 46 57 68 79 80 91",
    "03 14 25 36 47 58 69 70 81 92",
    "04 15 26 37 48 59 60 71 82 93",
    "05 16 27 38 49 50 61 72 83 94",
    "06 17 28 39 40 51 62 73 84 95",
    "07 18 29 30 41 52 63 74 85 96",
    "08 19 20 31 42 53 64 75 86 97",
    "09 10 21 32 43 54 65 76 87 98",
  ]);
  const DuLieuBang = () => {
    try {
      const tong = [
        {
          tong_0: 0,
          tong_d_0: 0,
          tong_1: 0,
          tong_d_1: 0,
          tong_2: 0,
          tong_d_2: 0,
          tong_3: 0,
          tong_d_3: 0,
          tong_4: 0,
          tong_d_4: 0,
          tong_5: 0,
          tong_d_5: 0,
          tong_6: 0,
          tong_d_6: 0,
          tong_7: 0,
          tong_d_7: 0,
          tong_8: 0,
          tong_d_8: 0,
          tong_9: 0,
          tong_d_9: 0,
        },
      ];

      DL.map((x, index) => {
        MangTongLo.map((y, index) => {
          if (
            window.localStorage.getItem(
              `GiaTriTheoNam/${Nam}/${x.ngay + "_" + x.thang + "_" + x.nam}`
            ) !== " "
          ) {
            removeArray(
              MangSo.map((z, _index) =>
                z.indexOf(
                  window.localStorage.getItem(
                    `GiaTriTheoNam/${Nam}/${
                      x.ngay + "_" + x.thang + "_" + x.nam
                    }`
                  )
                ) > 0
                  ? _index
                  : " "
              ),
              -1
            ).map((m) => {
              if (index === m) {
                switch (MangTongLo[m]) {
                  case "Tổng 0":
                    tong[0].tong_0 += 1;
                    break;
                  case "Tổng 1":
                    tong[0].tong_1 += 1;
                    break;
                  case "Tổng 2":
                    tong[0].tong_2 += 1;
                    break;
                  case "Tổng 3":
                    tong[0].tong_3 += 1;
                    break;
                  case "Tổng 4":
                    tong[0].tong_4 += 1;
                    break;
                  case "Tổng 5":
                    tong[0].tong_5 += 1;
                    break;
                  case "Tổng 6":
                    tong[0].tong_6 += 1;
                    break;
                  case "Tổng 7":
                    tong[0].tong_7 += 1;
                    break;
                  case "Tổng 8":
                    tong[0].tong_8 += 1;
                    break;
                  case "Tổng 9":
                    tong[0].tong_9 += 1;
                    break;
                  case "Tổng +0":
                    tong[0].tong_d_0 += 1;
                    break;
                  case "Tổng +1":
                    tong[0].tong_d_1 += 1;
                    break;
                  case "Tổng +2":
                    tong[0].tong_d_2 += 1;
                    break;
                  case "Tổng +3":
                    tong[0].tong_d_3 += 1;
                    break;
                  case "Tổng +4":
                    tong[0].tong_d_4 += 1;
                    break;
                  case "Tổng +5":
                    tong[0].tong_d_5 += 1;
                    break;
                  case "Tổng +6":
                    tong[0].tong_d_6 += 1;
                    break;
                  case "Tổng +7":
                    tong[0].tong_d_7 += 1;
                    break;
                  case "Tổng +8":
                    tong[0].tong_d_8 += 1;
                    break;
                  case "Tổng +9":
                    tong[0].tong_d_9 += 1;
                    break;
                }
              } else {
              }
            });
          } else {
          }
          //   console.log(tong);
          window.localStorage.setItem("tong", JSON.stringify(tong));
        });
      });
    } catch (error) {}
  };
  DuLieuBang();
  const KhoiTaoGiaTriMoi = () => {
    const DL = [];
    for (let i = 0; i < 12; i++) {
      for (let j = 0; j < getDaysOfMonth(i + 1, Nam); j++) {
        var updates = {};
        updates[
          "GiaTriTheoNam/" +
            Nam +
            "/" +
            (j + 1).toString() +
            "_" +
            (i + 1).toString() +
            "_" +
            Nam
        ] = " ";
        var starCountRef = firebase.ref().update(updates);
        window.location.href = "/";
        DL.push({
          thang: i + 1,
          ngay: j + 1,
          nam: parseInt(Nam),
        });
      }
    }
    SetDL(DL);
  };

  const DuLieuCoSan = () => {
    try {
      var check = false;
      // for (let i = 0; i < 12; i++) {
      //   for (let j = 0; j < getDaysOfMonth(i + 1, Nam); j++) {}
      // }
      firebase
        .ref("GiaTriTheoNam/" + Nam + "/1_1_" + Nam)
        .on("value", function (snapshot) {
          const dl = snapshot.val();
          console.log(dl === 0);
          if (dl === " ") {
            check = true;
            KhoiTaoGiaTriMoi();
          } else {
            check = false;
            const DL = [];
            for (let i = 0; i < 12; i++) {
              for (let j = 0; j < getDaysOfMonth(i + 1, Nam); j++) {
                DL.push({
                  thang: i + 1,
                  ngay: j + 1,
                  nam: parseInt(Nam),
                });
                firebase
                  .ref("GiaTriTheoNam/" + Nam + `/${j + 1}_${i + 1}_${Nam}`)
                  .on("value", function (snapshot) {
                    window.localStorage.setItem(
                      "GiaTriTheoNam/" + Nam + `/${j + 1}_${i + 1}_${Nam}`,
                      snapshot.val()
                    );
                  });
              }
            }
            SetDL(DL);
          }
        });
      console.log(check);
    } catch (error) {}
  };
  const ThanhDoiDuLieu = (dl, ngay, thang, nam) => {
    if (dl.length >= "12".length) {
      console.log("test");
      var updates = {};
      console.log("GiaTriTheoNam/" + nam + `/${ngay}_${thang}_${nam}`);
      updates["GiaTriTheoNam/" + nam + `/${ngay}_${thang}_${nam}`] = dl;
      var starCountRef = firebase.ref().update(updates);
    }
  };
  React.useEffect(() => {
    DuLieuCoSan();
    DuLieuBang();
  }, []);

  //   console.log(DL);
  console.log(dltong);
  return (
    <React.Fragment>
      <table className='table table-hover'>
        <thead>
          <tr>
            <th scope='col'>Số thứ tự</th>
            <th scope='col'>Ngày/Tháng/Năm</th>
            <th scope='col'>Nhập liệu</th>
            {MangTongLo.map((x, index) => (
              <th scope='col'>{x}</th>
            ))}
          </tr>
        </thead>
        <tbody>
          <tr scope='row'>
            <td>Kết quả</td>
            <td>------</td>
            <td>------</td>
            <td>{dltong[0].tong_0}</td>
            <td>{dltong[0].tong_1}</td>
            <td>{dltong[0].tong_2}</td>
            <td>{dltong[0].tong_3}</td>
            <td>{dltong[0].tong_4}</td>
            <td>{dltong[0].tong_5}</td>
            <td>{dltong[0].tong_6}</td>
            <td>{dltong[0].tong_7}</td>
            <td>{dltong[0].tong_8}</td>
            <td>{dltong[0].tong_9}</td>
            <td>{dltong[0].tong_d_0}</td>
            <td>{dltong[0].tong_d_1}</td>
            <td>{dltong[0].tong_d_2}</td>
            <td>{dltong[0].tong_d_3}</td>
            <td>{dltong[0].tong_d_4}</td>
            <td>{dltong[0].tong_d_5}</td>
            <td>{dltong[0].tong_d_6}</td>
            <td>{dltong[0].tong_d_7}</td>
            <td>{dltong[0].tong_d_8}</td>
            <td>{dltong[0].tong_d_9}</td>
          </tr>
          {DL.map((x, index) => (
            <tr>
              <td scope='row'>{index}</td>
              <td>{x.ngay + "/" + x.thang + "/" + x.nam}</td>
              <td>
                <input
                  className='form-control'
                  id='box-nhap'
                  style={{
                    backgroundColor: "#6c7ae0",
                    width: "100px",
                    color: "white",
                  }}
                  placeholder={window.localStorage.getItem(
                    `GiaTriTheoNam/${Nam}/${
                      x.ngay + "_" + x.thang + "_" + x.nam
                    }`
                  )}
                  onChange={(e) => {
                    const dl = e.target.value;
                    const ngay = x.ngay;
                    const thang = x.thang;
                    const nam = x.nam;
                    ThanhDoiDuLieu(dl, ngay, thang, nam);
                  }}></input>
              </td>
              {MangTongLo.map((y, index) => (
                <td>
                  {window.localStorage.getItem(
                    `GiaTriTheoNam/${Nam}/${
                      x.ngay + "_" + x.thang + "_" + x.nam
                    }`
                  ) !== " "
                    ? removeArray(
                        MangSo.map((z, _index) =>
                          z.indexOf(
                            window.localStorage.getItem(
                              `GiaTriTheoNam/${Nam}/${
                                x.ngay + "_" + x.thang + "_" + x.nam
                              }`
                            )
                          ) > 0
                            ? _index
                            : " "
                        ),
                        -1
                      ).map((m) => (index === m ? MangTongLo[m] : " "))
                    : " "}
                </td>
              ))}
            </tr>
          ))}
        </tbody>
      </table>
    </React.Fragment>
  );
}

export default Main;
