/** @format */

import "./App.css";
import React from "react";
import Main from "./Main";
import firebase from "./firebase";
function App() {
  const [Nam, SetNam] = React.useState(window.localStorage.getItem("Nam"));
  const GetData = () => {
    firebase.ref("Nam").on("value", function (snapshot) {
      window.localStorage.setItem("Nam", snapshot.val());
    });
  };
  const GetDulieu = async (e) => {
    SetNam(e.target.value);
    if (e.target.value.length >= "1999".length) {
      var updates = {};
      console.log(e.target.value);
      updates["Nam"] = e.target.value;
      var starCountRef = firebase.ref().update(updates);
      window.location.href = "/";
    }
  };
  React.useEffect(() => {
    GetData();
  }, []);
  return (
    <React.Fragment>
      <div className='text-center'>
        <h2>Tool thống kê</h2>
      </div>
      <label style={{ marginLeft: "20px" }}>Năm</label>
      <input
        className='form-control'
        style={{
          backgroundColor: "#6c7ae0",
          width: "100px",
          color: "white",
          marginLeft: "20px",
        }}
        onChange={GetDulieu}
        value={Nam}></input>
      <Main></Main>
    </React.Fragment>
  );
}

export default App;
